import React, { useEffect, useRef, useState } from "react";

const PhotoAnimate = ({ src, alt, delay = 0, threshold = 0.1, style = {}, duration = 2 }) => {
  const [inView, setInView] = useState(false);
  const ref = useRef(null);

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setInView(true);
        } else {
          setInView(false);
        }
      },
      { threshold }
    );

    if (ref.current) {
      observer.observe(ref.current);
    }

    return () => {
      if (ref.current) {
        observer.unobserve(ref.current);
      }
    };
  }, [threshold]);

  return (
    <img
      ref={ref}
      src={src}
      alt={alt}
      style={{
        ...style, // Исходный стиль изображения
        transform: inView ? "translateY(0)" : "translateY(100px)", // Анимация появления
        opacity: inView ? 1 : 0, // Плавное проявление
        transition: `transform ${duration}s ease-out ${delay}s, opacity ${duration}s ease-out ${delay}s`,
      }}
    />
  );
};

export default PhotoAnimate;
