import React, { useEffect, useRef, useState } from "react";

const ParagraphAnimation = ({ text }) => {
  const [inView, setInView] = useState(false); // Контролируем видимость
  const ref = useRef(null); // Ссылка на DOM-элемент

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setInView(true); // Устанавливаем видимость, если элемент в области видимости
        }
      },
      { threshold: 0.1 } // Триггер срабатывает, когда элемент виден на 10%
    );

    if (ref.current) {
      observer.observe(ref.current);
    }

    return () => {
      if (ref.current) {
        observer.unobserve(ref.current);
      }
    };
  }, []);

  return (
    <p
      ref={ref} // Привязываем ссылку к элементу
      style={{
        opacity: inView ? 1 : 0,
        transform: inView ? "translateY(0)" : "translateY(100%)",
        transition: "opacity 0.7s ease-out, transform 0.7s ease-out",
      }}
    >
      {text}
    </p>
  );
};

export default ParagraphAnimation;
