import React, { useState, useEffect } from "react";

const Page1 = () => {
  const [phase, setPhase] = useState("years"); // Фаза: "years" или "text"
  const [year, setYear] = useState(12); // Текущий год
  const [index, setIndex] = useState(0); // Индекс текущего текста
  const [showScrollIcon, setShowScrollIcon] = useState(false); // Показ scroll-icon

  // Тексты для отображения
  const texts = [
    "We are JET24",
    "Just Enjoy Time",
    "The ambitious team and <br /> visionary individuals with <br /> big passion for aviation and sky.",
  ];

  // Анимация смены годов
  useEffect(() => {
    if (phase === "years") {
      const yearInterval = setInterval(() => {
        setYear((prev) => {
          if (prev < 24) return prev + 1;
          clearInterval(yearInterval); // Остановка анимации годов
          setTimeout(() => setPhase("text"), 1000); // Переход к текстам через 1 секунду
          return prev;
        });
      }, 300); // Смена годов каждые 300 мс
      return () => clearInterval(yearInterval);
    }
  }, [phase]);

  // Анимация смены текста
  useEffect(() => {
    if (phase === "text") {
      const textInterval = setInterval(() => {
        setIndex((prev) => {
          if (prev < texts.length - 1) return prev + 1;
          clearInterval(textInterval); // Остановка на последнем тексте
          setTimeout(() => setShowScrollIcon(true), 500); // Показ scroll-icon через 1 секунду
          return prev;
        });
      }, 1500); // Смена текста каждые 1.5 секунды
      return () => clearInterval(textInterval);
    }
  }, [phase, texts.length]);

  return (
    <div style={styles.page} id="main">
      <div className="we__container">
        {/* Анимация годов */}
        {phase === "years" && (
          <div className="we__year-container">
            <span className="we__start-text">2012 — 20</span>
            <div className="we__scroll-wrapper">
              <div
                className="we__scroll-content"
                style={{
                  transform: `translateY(-${(year - 12) * 4}rem)`, // Сдвиг в rem, соответствующий font-size
                }}
              >
                {Array.from({ length: 13 }, (_, i) => (
                  <div key={i} className="we__scroll-item">
                    {i + 12}
                  </div>
                ))}
              </div>
            </div>
          </div>
        )}

        {/* Анимация текста */}
        {phase === "text" && (
          <div className="we__text-container">
            {texts.map((text, i) => (
              <p
                key={i}
                className={`we__text ${index === i ? "visible" : ""}`}
                style={{ display: index === i ? "block" : "none" }}
                dangerouslySetInnerHTML={{ __html: text }} // Используем для вставки HTML
              ></p>
            ))}
          </div>
        )}

        {/* Контейнер для scroll-icon */}
        <div
          className={`scroll-icon-container ${showScrollIcon ? "visible" : ""}`}
        >
          <svg
            className="scroll-icon"
            width="26"
            height="42"
            viewBox="0 0 26 42"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            {/* Контур мышки */}
            <rect
              x="1"
              y="1"
              width="24"
              height="40"
              rx="12"
              stroke="#616161"
              strokeWidth="1.5"
            ></rect>
            {/* Анимированное колесико */}
            <rect
              className="dot"
              x="11"
              y="6"
              width="4"
              height="6.66667"
              rx="2"
              fill="#9E9E9E"
            ></rect>
          </svg>
        </div>
      </div>
    </div>
  );
};

const styles = {
  page: {
    minWidth: "100vw",
    height: "100vh",
    position: "relative",
  },
};

export default Page1;
