import "./style.css";

import icon1 from "./icon/1 (2).svg";
import icon2 from "./icon/2 (2).svg";
import icon3 from "./icon/3 (2).svg";
import img from "./img/stock-photo-private-jet-is-on-runway-2395467135 1.png";
import img2 from "./img/Mask group (5).png";
import React from "react";
import AnimatedText from "./Animatedtext";
import PhotoAnimate from "./Photoanimate";
import ParagraphAnimation from "./ParagraphAnimation";

const Page3 = () => {
  return (
    <div style={styles.page} id="about">
      <div className="concierge-page">
        <div className="left-section ab__left-section ">
          <h1>
            <AnimatedText text="About us Why Jet24" />
          </h1>
          <ParagraphAnimation text="We are the professional team with the experience in business aviation for more than 10 years. We offer you personal support 24/7, own dispatch and flight control service, off-market offers right for your needs and budget, concierge service." />


          <div className="icon-section ab__icon-section">
            <PhotoAnimate
              src={icon1}
              alt="Rent Villa Icon"
              delay={0.2}
              animationStyle={{ maxWidth: "50px" }}
            />
            <PhotoAnimate
              src={icon2}
              alt="Rent Yacht Icon"
              delay={0.2}
              animationStyle={{ maxWidth: "50px" }}
            />
            <PhotoAnimate
              src={icon3}
              alt="Gastronomy Icon"
              delay={0.2}
              animationStyle={{ maxWidth: "50px" }}
            />
          </div>
        </div>
      </div>

      <div className="right-section">
        <div className="image large-image ab__large-image">
        <PhotoAnimate
  src={img}
  alt="Dining Setup"
  delay={1.0}
/>
        </div>
        <div className="image small-image ab__small-image">
        <PhotoAnimate
  src={img2}
  alt="Yacht Charter"
  delay={1.2}
/>
        </div>
      </div>
    </div>
  );
};

const styles = {
  page: {
    minWidth: "100vw",
    height: "100vh",
    position: "relative",
  },
};

export default Page3;
