import React from "react";
import "./style.css";
import icon1 from "./icon/1.svg";
import icon2 from "./icon/2.svg";
import icon3 from "./icon/3.svg";
import img from "./img/Mask group.png";
import img2 from "./img/image 3.png";
import AnimatedText from "./Animatedtext";
import PhotoAnimate from "./Photoanimate";
import ParagraphAnimation from "./ParagraphAnimation";

const Page4 = () => {
  return (
    <div style={styles.page} id="air">
      <div className="concierge-page air_concierge-page">
        <div className="left-section">
          <h1>
            <AnimatedText text="Aircraft Management" />
          </h1>
          <ParagraphAnimation text="We provide full operational management of your private jet and arrange its commercial sales and subcharter on demand. You can do with us aircraft purchase and sale, technical, financial and legal audit, crew management." />
          <ParagraphAnimation text="We care for details without unnecessary costs." />

          <div className="icon-section ms-5">
            <PhotoAnimate
              src={icon1}
              alt="Rent Villa Icon"
              delay={0.2}
              animationStyle={{ maxWidth: "50px" }}
            />
            <PhotoAnimate
              src={icon2}
              alt="Rent Yacht Icon"
              delay={0.2}
              animationStyle={{ maxWidth: "50px" }}
            />
            <PhotoAnimate
              src={icon3}
              alt="Gastronomy Icon"
              delay={0.2}
              animationStyle={{ maxWidth: "50px" }}
            />
          </div>
        </div>
      </div>

      <div className="right-section">
        <div className="image large-image">
          <PhotoAnimate
            src={img}
            alt="Dining Setup"
            delay={1.0}
            animationStyle={{ maxWidth: "100%" }}
          />
        </div>
        <div className="image small-image2">
          <PhotoAnimate
            src={img2}
            alt="Villa"
            delay={1.0}
            animationStyle={{ maxWidth: "100%" }}
          />
        </div>
      </div>
    </div>
  );
};

const styles = {
  page: {
    minWidth: "100vw",
    height: "100vh",
    position: "relative",
  },
};

export default Page4;
