import React, { useEffect, useRef, useState } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import Page1 from "./Page1";
import Page2 from "./Page2";
import Page3 from "./Page3";
import Page4 from "./Page4";
import Page5 from "./Page5";
import Page6 from "./Page6";
import Page7 from "./Page7";
import Footer from "../Footer/Footer";

gsap.registerPlugin(ScrollTrigger);

const HorizontalScroll = () => {
  const containerRef = useRef(null);
  const [currentPage, setCurrentPage] = useState(1); // Состояние текущей страницы

  useEffect(() => {
    const container = containerRef.current;
    const content = container.querySelector(".scroll-content");

    let proxy = { scrollTop: 0 };
    let scrollWidth = content.scrollWidth - window.innerWidth;

    // Анимация горизонтального скролла
    gsap.to(proxy, {
      scrollTop: scrollWidth,
      ease: "power2.out",
      onUpdate: () => {
        gsap.set(content, { x: -proxy.scrollTop });
      },
      scrollTrigger: {
        trigger: container,
        start: "top top",
        end: `+=${scrollWidth}`,
        scrub: 1,
        pin: true,
        onUpdate: (self) => {
          const progress = self.progress;
          const totalPages = content.children.length;
          const currentPageIndex = Math.floor(progress * totalPages);
          setCurrentPage(currentPageIndex + 1);
        },
      },
    });

    // Настройка анимаций для отдельных блоков
    const elements = content.querySelectorAll("[data-gsap='fade-up']");
    elements.forEach((el) => {
      gsap.fromTo(
        el,
        { autoAlpha: 0, y: 50 },
        {
          autoAlpha: 1,
          y: 0,
          duration: 1,
          scrollTrigger: {
            trigger: el,
            start: "top 80%", // Анимация начинается, когда верх элемента входит в нижние 20% экрана
            end: "top 50%", // Анимация заканчивается, когда верх элемента находится в центре экрана
            toggleActions: "play none none reverse", // Поведение анимации
            scrub: 0.5,
          },
        }
      );
    });

    return () => {
      ScrollTrigger.kill();
    };
  }, []);

  return (
    <div
      className="scroll-container"
      ref={containerRef}
      style={styles.container}
    >
      <div className="scroll-content" style={styles.content}>
        <Page1 />
        <div style={styles.page2}>
          <Page7 />
        </div>
        <div style={styles.page2}>
          <Page3 />
        </div>
        <div style={styles.page2}>
          <Page2 />
        </div>
        <div style={styles.page2}>
          <Page4 />
        </div>
       
          <Page5 />
        <Page6 />
      </div>
      <Footer />
    </div>
  );
};

const styles = {
  container: {
    position: "relative",
    width: "100%",
    height: "100vh",
  },
  content: {
    display: "flex",
    height: "100vh",
  },

  page2: {
    marginRight: "300px", // Добавляем расстояние между Page2 и Page3
  },
};

export default HorizontalScroll;
