import React from "react";
import AnimatedText from "./Animatedtext";
import PhotoAnimate from "./Photoanimate";
import ParagraphAnimation from "./ParagraphAnimation";
import icon1 from "./icon/JET24_Icon_Chartersjet-01 1.svg";
import icon2 from "./icon/JET24_Icon_ChartersHeli-01 1.svg";
import icon3 from "./icon/JET24_Icon_MedCorporate_2-01 1.svg";
import img from "./img/1.png";
import img2 from "./img/2.png";
import img3 from "./img/3.png";
import "./style.css";

const Page2 = () => {
  return (
    <div style={styles.page} id="chart">
      <div className="concierge-page">
        <div className="left-section chart__left-section">
          <h1>
            <AnimatedText text="Charters" />
          </h1>
          <ParagraphAnimation text="JET24 does the worldwide private charters from the top leading aviation operators for personal and business purposes, works with corporate VIP-airliners and helicopters, medical flights." />

          <div className="icon-section">
            <PhotoAnimate
              src={icon1}
              alt="Rent Villa Icon"
              delay={0.2}
              animationStyle={{ maxWidth: "50px" }}
            />
            <PhotoAnimate
              src={icon2}
              alt="Rent Yacht Icon"
              delay={0.2}
              animationStyle={{ maxWidth: "50px" }}
            />
            <PhotoAnimate
              src={icon3}
              alt="Gastronomy Icon"
              delay={0.2}
              animationStyle={{ maxWidth: "50px" }}
            />
          </div>
        </div>

        <div className="right-section chart__right-section">
          <div className="image large-image chart__large-image">
            <PhotoAnimate
              src={img}
              alt="Dining Setup"
              delay={1.0}
              animationStyle={{ maxWidth: "100%" }}
            />
          </div>
          <div className="image small-image2 chart__small-image2">
            <PhotoAnimate
              src={img2}
              alt="Villa"
              delay={1.2}
              animationStyle={{ maxWidth: "100%" }}
            />
          </div>
          <div className="image small-image chart__small-image">
            <PhotoAnimate
              src={img3}
              alt="Yacht Charter"
              delay={1.4}
              animationStyle={{ maxWidth: "100%" }}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

const styles = {
  page: {
    minWidth: "100vw",
    height: "100vh",
    position: "relative",
  },
};

export default Page2;
