import "./style.css";
import icon1 from "./icon/JET24_Icon_RentVilla-01 1.svg";
import icon2 from "./icon/JET24_Icon_RentYacht-01 1.svg";
import icon3 from "./icon/JET24_Icon_Gastronomy-01 1.svg";

import img from "./img/20443151 1.png";
import img2 from "./img/image 2.png";
import img3 from "./img/29434 1.png";
import React, { useEffect } from "react";
import AnimatedText from "./Animatedtext";
import PhotoAnimate from "./Photoanimate";
import ParagraphAnimation from "./ParagraphAnimation";

const Page5 = () => {
  useEffect(() => {
    // Используем IntersectionObserver для контроля видимости элементов
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            entry.target.classList.add("aos-animate");
          } else {
            entry.target.classList.remove("aos-animate");
          }
        });
      },
      { threshold: 0.5 }
    );

    const elements = document.querySelectorAll("[data-aos]");
    elements.forEach((el) => observer.observe(el));

    return () => observer.disconnect();
  }, []);

  return (
    <div style={styles.page} id="con">
      <div className="concierge-page">
        <div className="left-section conci">
          <h1 >
             <AnimatedText text="Concierge Service" />
          </h1>
          <p>
            <ParagraphAnimation text="Our partnership network around the world allows us to arrange
            concierge service and daily lifestyle support, accommodation service
            in hotels, villas and chalet, yacht charters, ground and helicopter
            transfers, tickets to sport and cultural events, reservations at top
            restaurants."/>
          </p>
          <p> <ParagraphAnimation text="No membership fee."/></p>

          <div className="icon-section ms-5">
            <PhotoAnimate
              src={icon1}
              alt="Rent Villa Icon"
              delay={0.2}
              animationStyle={{ maxWidth: "50px", marginBottom: "20px" }}
            />
            <PhotoAnimate
              src={icon2}
              alt="Rent Yacht Icon"
              delay={0.2}
              animationStyle={{ maxWidth: "50px", marginBottom: "20px" }}
            />
            <PhotoAnimate
              src={icon3}
              alt="Gastronomy Icon"
              delay={0.2}
              animationStyle={{ maxWidth: "50px", marginBottom: "20px" }}
            />
          </div>
        </div>
      </div>

      <div className="right-section">
        <div className="image large-image">
          <PhotoAnimate
            src={img}
            alt="Dining Setup"
            delay={1.0}
            animationStyle={{ maxWidth: "100%", marginBottom: "20px" }}
          />
        </div>
        <div className="image small-image2">
          <PhotoAnimate
            src={img2}
            alt="Villa"
            delay={1.3}
            animationStyle={{ maxWidth: "100%", marginBottom: "20px" }}
          />
        </div>
        <div className="image small-image">
          <PhotoAnimate
            src={img3}
            alt="Yacht Charter"
            delay={1.5}
            animationStyle={{ maxWidth: "100%", marginBottom: "20px" }}
          />
        </div>
      </div>
    </div>
  );
};

const styles = {
  page: {
    minWidth: "100vw",
    height: "100vh",
    position: "relative",
  },
};

export default Page5;
