import "./style.css";
import { FaTelegramPlane, FaWhatsapp } from "react-icons/fa";
import React from "react";
import AnimatedText from "./Animatedtext";
import ParagraphAnimation from "./ParagraphAnimation";

const Page6 = () => {
  return (
    <div style={styles.page} id="just">
      <main className="main-content main-content-say">
        <h1 className="title">
          <AnimatedText text="JUST SAY US HELLO" />
        </h1>
        <div className="contact-info">
          <div className="contact">
            <h3><AnimatedText text="Contact"  /></h3>
            <ParagraphAnimation text="+380 (73) 887 87 80" />
            <ParagraphAnimation text="sales@jet24.aero" />
          </div>
          <div className="follow">
         <h3>   <AnimatedText text="Follow us" /></h3>
            <div className="social-icons">
              <FaWhatsapp className="icon" />
              <FaTelegramPlane className="icon" />
            </div>
          </div>
        </div>
      </main>
    
    </div>
  );
};

const styles = {
  page: {
    minWidth: "100vw",
    height: "100vh",
    position: "relative",
  },
};

export default Page6;
