import React, { useEffect, useRef, useState } from "react";

const AnimatedText = ({ text }) => {
  const [inView, setInView] = useState(false); // Контролируем видимость
  const ref = useRef(null); // Ссылка на DOM-элемент

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setInView(true); // Устанавливаем видимость, если элемент в области видимости
        }
      },
      { threshold: 0.1 } // Триггер срабатывает, когда элемент виден на 10%
    );

    if (ref.current) {
      observer.observe(ref.current);
    }

    return () => {
      if (ref.current) {
        observer.unobserve(ref.current);
      }
    };
  }, []);

  const splitText = (text) =>
    text.split("").map((char, index) => ({
      char,
      delay: 0.4 + index * 0.05, // Устанавливаем задержку для каждой буквы
    }));

  return (
    <span
      ref={ref} // Привязываем ссылку к элементу
      style={{
        display: "inline-block",
        overflow: "hidden",
        height: "73px",
      }}
    >
      {splitText(text).map(({ char, delay }, index) => (
        <span
          key={index}
          style={{
            display: "inline-block",
            transform: inView ? "translateY(0)" : "translateY(100%)",
            opacity: inView ? 1 : 0,
            transition: `transform 0.5s ease-out ${delay}s, opacity 0.5s ease-out ${delay}s`,
          }}
        >
          {char === " " ? "\u00A0" : char} {/* Обрабатываем пробелы */}
        </span>
      ))}
    </span>
  );
};

export default AnimatedText;
