import "./style.css";

import img1 from "./img/JET24_Icon_FullOperation-01 1.svg";
import img2 from "./img/JET24_Icon_Details-01 1.svg";
import img3 from "./img/JET24_Icon_Crew-01 2.svg";

import React from "react";
import AnimatedText from "./Animatedtext";
import PhotoAnimate from "./Photoanimate";
import ParagraphAnimation from "./ParagraphAnimation";

const Page7 = () => {
  return (
    <div style={styles.page} id="make">
      <div>
        <div className="requ__logo-left">
          <PhotoAnimate
            src={img1}
            alt="Full Operation"
            delay={0.2}
            animationStyle={{ maxWidth: "80px", marginBottom: "20px" }}
          />
          <PhotoAnimate
            src={img2}
            alt="Details"
            delay={0.2}
            animationStyle={{ maxWidth: "80px", marginBottom: "20px" }}
          />
          <PhotoAnimate
            src={img3}
            alt="Crew"
            delay={0.2}
            animationStyle={{ maxWidth: "80px", marginBottom: "20px" }}
          />
        </div>
        <div className="request-form-container">
        <h2 >
        <AnimatedText text="MAKE REQUEST" />
          </h2>
         
          <p>
          <ParagraphAnimation
            text="We will contact you to confirm all the details"
            delay={1.6}
          />
          </p>
          <form className="request-form">
            <input
              type="text"
              placeholder="Your name"
              required
              className="request-input"
            />
            <input
              type="email"
              placeholder="Email"
              required
              className="request-input"
            />
            <input
              type="tel"
              placeholder="Phone (with country code)"
              required
              className="request-input"
            />
            <textarea
              className="request-textarea"
              placeholder="Describe your request"
              required
            ></textarea>
              <button type="submit" className="request__btn">
                Send Request
              </button>
          </form>
        </div>
      </div>
    </div>
  );
};

const styles = {
  page: {
    minWidth: "100vw",
    height: "100vh",
    position: "relative",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    flexDirection: "column",
  },
};

export default Page7;
